import React, {useEffect, useState} from "react";

const WhatIsKyc = ({ nextPage }) => {
    const [classState, setClassState] = useState('');

    useEffect(() => {
        setClassState('css--what-is-kyc-anim')
    }, [])

    return (
        <div id="kyc" className="flex justify-center content-center h-screen">
            <div className="flex justify-center items-center flex-wrap overflow-hidden">
                <div className="container" style={{zIndex:'10'}}>
                    <div className="md:grid-cols-3 grid-cols-1 gap-4 flex flex-col-reverse md:flex-row justify-center">
                        <div className="col-span-1 md:col-span-2 flex justify-center">
                            <div className="flex flex-col justify-center relative">
                                <div className="css--kyc-header-second mb-3 md:mb-8">What is KYC?</div>
                                <div>The know your customer or know your client guidelines in financial services require that professionals make an effort to verify the identity, suitability, and risks involved with maintaining a business relationship.</div>
                                <div className="pt-3 md:pt-8">
                                    <button className="btn" onClick={nextPage}>More</button>
                                </div>
                            </div>
                        </div>
                        <div className="flex justify-center items-center  w-7/12 md:w-full m-auto">
                            <div className={'css--what-is-kyc-triangle ' + classState}  style={{minHeight:'15vh'}}>
                                <span/>
                                <span/>
                                <span/>
                                <span/>
                                <span/>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}
export default WhatIsKyc;
