import React from 'react';
import {Helmet} from 'react-helmet';
import {useStaticQuery, graphql} from 'gatsby';

const SEO = ({post = {}}) => {
    const data = useStaticQuery(graphql`
      {
        site {
          siteMetadata {
            title
            description
            baseUrl
          }
        }
      }
    `);

    const defaults = data.site.siteMetadata;

    if (defaults.baseUrl === '' && typeof window !== 'undefined') {
        defaults.baseUrl = window.location.origin;
    }

    if (defaults.baseUrl === '') {
        console.error('Please set a baseUrl in your site metadata!');
        return null;
    }

    const title = post.title || defaults.title;
    const description = post.description || defaults.description;
    const url = new URL(post.path || '', defaults.baseUrl);
    const image = (post.image) ? new URL(post.image, defaults.image) : '/img/logo/kalmars_social_padding.png';
    const author = post.author?.twitter || (defaults.author || 'KALMARS');

    return (
        <Helmet>
            <title>{title}</title>
            <link rel="canonical" href={url}/>
            <meta name="description" content={description}/>
            {image && <meta name="image" content={image}/>}

            <meta property="og:url" content={url}/>
            <meta property="og:type" content="article"/>
            <meta property="og:title" content={title}/>
            <meta property="og:description" content={description}/>
            {image && <meta property="og:image" content={image}/>}

            <meta name="twitter:card" content="summary_large_image"/>
            <meta name="twitter:creator" content={author}/>
            <meta name="twitter:title" content={title}/>
            <meta name="twitter:description" content={description}/>
            {image && <meta name="twitter:image" content={image}/>}

            <meta name="keywords" content="Kalmars, KALMARS, Kalmars Group, KALMARS Group, /KALMARS/, /KALMARS/ Group, Web Development, IoT , Internet of things, mājaslapas izstrāde, web consulting, Gudrās iekārtas, programmēšana, aplikāciju izstrāde"/>
            <meta name="robots" content="index, follow"/>
            <meta name="revisit-after" content="14 days"/>
            <meta name="author" content="/KALMARS/ Group"/>

            <link rel="shortcut icon" href="/favicons/favicon.ico" />
        </Helmet>
    );
};

export default SEO;